=wo
  will-change: opacity

+font("ProximaNova Light", proximanova-light)
+font("ProximaNova Regular", proximanova-reg)
+font("ProximaNova Bold", proximanova-bold)
+font("ProximaNova XBold", proximanova-xbold)
+font("ProximaNova-Black", ProximaNova-Black)
+font("Geometria Bold", geometria-bold)

=light
  font-family: "ProximaNova Light", Arial, sans-serif
=reg
  font-family: "ProximaNova Regular", Arial, sans-serif
=bold
  font-family: "ProximaNova Bold", Arial, sans-serif
=xbold
  font-family: "ProximaNova XBold", Arial, sans-serif
=black
  font-family: "ProximaNova-Black", Arial, sans-serif
=geobold
  font-family: "Geometria Bold", Arial, sans-serif

// body
body
  +reg
  min-width: 480px
  font-size: 14px
  line-height: 1.3


//reset
a
  text-decoration: none

h1, h2, h3, h4, h5, h6, em, b, strong, i, th, td
  font-weight: normal
  font-style: normal

// variables
$yellow: #fff600
$l_yellow: #fffb86
$green: #00ca7a
$blue: #00c2fd
$d_blue: #1e2e5d
$l_blue: #f9faff
$l_green: #00e0bf
$turquoise: #00d9cd
$gray: #d2d2d2
$d_gray: #f4f5f7
$d_red: #fc1147
$red: #ff322c
$l_red: #fed6d6

$c_input: #353535
$c_inp-lab: #c3c3c3
$c_tab-card: $c_input
$c_blue: #3276fb
$c_error-text: #fc034a

$bg_f: #335fa2
$bg_tw: #24a7df
$bg_in: #006b9c
$bg_g: #de4d3a

$bd-input: #dedddd
$bd-bord-blue: #00ceff
$bd-yellow: #eede7c

p
  &.is-yellow
    color: $yellow

// extend
.in
  margin: 0 auto
  padding: 0 15px
  max-width: 920px
.in-sm
  margin: 0 auto
  max-width: 455px
%in
  @extend .in
%in-sm
  @extend .in-sm
.header
  &__inner
    max-width: 960px
    margin: 0 auto
    padding: 0 5px 0 35px
    +clr

.nav
  float: right
  +r(920)
    float: none
    margin: 32px 0 0 -35px
    text-align: center
  a
    margin: 0 25px
    display: inline-block
    vertical-align: top
    color: #fff
    font-size: 12px
    line-height: 20px
    text-transform: uppercase
    letter-spacing: 0.1em
    position: relative
    padding: 45px 0 30px
    &.is-active
      +xbold
    &:before
      height: 4px
    +r(920)
      padding: 17px 0
      &:before
        top: 100%

.logo
  float: left
  +clr
  max-width: 400px
  margin: 28px 0 0
  +r(920)
    float: none
  h1
    +xbold
    font-size: 38px
    color: $yellow
    line-height: 30px
    float: left
    padding: 6px 17px 2px 0
    border-right: 1px solid $yellow
    margin: 0 17px 0 0
  p
    overflow: hidden
    +light
    color: #fff
    font-size: 14px
    line-height: 16px
    padding: 4px 0 2px

.top
  position: relative
  color: #fff
  background: url(../img/main-v2.jpg)
  +cover
  min-height: 800px
  position: relative
  +r(920)
    padding-top: 45px
    min-height: 0

.step
  padding-bottom: 30px
  text-align: center
  height: 705px
  +r(920)
    padding: 52px 15px 45px
  &_one
    .step__block
      padding: 30px 39px 36px 34px
      max-width: 415px
  &_two
    .step__in
      margin: 0 auto
      width: 316px
  &__title
    font-size: 16px
    line-height: 1
    color: #fff
    +bold
    text-transform: uppercase
    margin-bottom: 30px
  &__block
    background: white
    border: 1px solid $yellow
    margin: 0 auto 30px
    &.is-mod
      &:before
        background: url(../img/block-bg.png) repeat-x 0 0
  &__btn
    padding: 5px 0 30px
  &__row
    padding: 19px 10px
    color: #000
    border-bottom: 1px solid $d_gray
    box-shadow: inset 0 1px 0 #fff
    &.is-blue
      padding: 14px 10px 18px
      background: $l_blue
  &__code,
  &__bold
    margin-top: 7px
    +bold
  &__bold
    .price
      font-size: 20px
  &__in
    width: 873px
    font-size: 14px
    vertical-align: middle
    display: inline-block
    +r(920)
      width: auto
    &.is-mod
      width: 440px
      +r(920)
        width: auto
    & > .title-middle
      margin-bottom: 20px
  &__wrap
    width: 324px
    .title-middle
      margin-bottom: 20px
  &__wr
    padding: 38px 28px
    color: #000
    .title-middle
      margin-bottom: 15px
      color: #000
  &__text
    margin-top: 28px
  &__nav
    margin: 20px 0 40px
    font-size: 12px
    a
      color: $yellow
      text-transform: uppercase
      &:hover
        border-color: $yellow
  &__inform
    margin: 0 -65px 20px
    width: 440px
  .price
    margin: 18px 0 28px
  .input-field
    margin-bottom: 14px
  .sms-code
    margin-top: 25px

.cards
  margin-bottom: 38px
  &__col
    +r(920)
      margin: 35px 0 0

.card
  background: white
  border: 1px solid $yellow
  &__in
    padding: 32px 39px 32px 32px
  &__icon
    color: $turquoise
    +reg
  &__block
    +r(920)
      width: 320px
  &__label
    top: 5px
    color: $c_inp-lab
  &__question
    color: $c_blue
    &:hover
      border-color: $c_blue
  &__cont
    padding: 32px 39px 44px 32px
  &__ul
    width: 100%
    li
      border-left: 1px solid #fff
      background: $yellow
      &:hover,
      &.is-active
        background: $l_yellow
      &:first-child
        a
          padding-left: 35px
        .icon
          left: 9px
    a
      padding: 7px 45px 10px 30px
      height: 53px
      color: $c_tab-card
      .icon
        top: 11px
        left: 11px

.price
  font-size: 16px
  +bold
  strong
    font-size: 20px

.rates
  padding: 48px 0 51px
  text-align: center
  +r(920)
    padding: 32px 0 40px
  &__in
    @extend %in-sm
  &__but
    margin-top: 30px
    +r(920)
      margin-top: 20px
  strong
    +bold
  p
    line-height: 1.8

.answer
  padding: 34px 0 50px
  color: #fff
  background: url(../img/answer-bg.png) no-repeat 50% 50%
  background-size: cover
  &__in
    @extend %in-sm
.annex
  padding: 28px 0
  text-align: center
  &__in
    @extend %in
  &__row
    margin-top: 20px
    font-size: 0
  .app
    margin-left: 16px
    &:first-child
      margin-left: 0
.question
  padding: 40px 0 49px
  &__in
    @extend %in
  .title
    margin-bottom: 22px
  & + .line
    +r(920)
      display: none

.btn
  position: relative
  font-size: 14px
  vertical-align: top
  display: inline-block
  &_shad
    &:hover
      &:before
        bottom: 0
    &:active
      &:before
        top: 0
      span
        box-shadow: inset 2px 2px 5px rgba(#000, 0.2)
    &:before
      content: ""
      position: absolute
      top: 2px
      left: 5px
      right: 5px
      bottom: -2px
      transition: bottom .2s
      background: rgba(#f05724, 0.5)
    span
      position: relative
      min-width: 247px
      display: block
      +geobold
      color: #fff
      line-height: 1
      padding: 18px 60px 17px
      transition: box-shadow .1s
      background: linear-gradient(4deg, #ff0042 0%, #ef5c23 100%)
      text-transform: uppercase
      letter-spacing: 0.1em
  &_bord
    padding: 17px 42px
    +bold
    color: #000
    transition: border-color .2s
    border: 1px solid $l_green
    +r(920)
      padding: 11px 42px
    &:hover
      border-color: darken($l_green, 20%)
  &_bord-red
    padding: 12px 24px
    min-width: 227px
    font-size: 12px
    color: #fff
    +geobold
    text-transform: uppercase
    background-color: transparent
    transition: border-color .2s
    border: 2px solid $red
    &:hover
      border-color: darken($red, 20%)

.input
  +reg
  &:focus
    border-color: $yellow

.error-xext
  color: $c_error-text

.input-field
  .input
    padding-right: 50px

.currency
  font-size: 15px
  color: $c_input

.input-yellow
  width: 100%
  border: 1px solid $bd-yellow
  color: $c_input
  font-size: 15px
  padding: 14px 20px 13px
  +reg

.checkbox
  position: relative
  display: inline-block
  vertical-align: top
  cursor: pointer
  user-select: none
  input
    position: absolute
    visibility: hidden
    opacity: 0
    top: 0
    left: 0
    &:checked + span
      i
        top: 10px
        visibility: visible
        opacity: 1
  span
    color: $yellow
    font-size: 15px
    line-height: 1
    padding: 7px 0 6px 45px
    display: inline-block
    &:before
      content: ""
      position: absolute
      top: 0
      left: 0
      border: 1px solid $yellow
      border-radius: 50%
      width: 28px
      height: 28px
    b
      +bold
    i
      font-size: 11px
      color: #fff
      position: absolute
      top: 5px
      left: 7px
      visibility: hidden
      opacity: 0
      +wo
      transition: all 0.2s

// line
.line
  background-color: #d5dcdf
  background-image: linear-gradient(4deg, #00beae 0%, #00ca7a 100%)

// is-mb
.is-mb
  +r(920)
    display: none

// app
.app
  vertical-align: top
  +wo
  transition: opacity .2s
  display: inline-block
  &:hover
    opacity: .8

// title
.title
  margin-bottom: 30px
  font-size: 22px
  text-align: center
  text-transform: uppercase
  +bold
  +r(920)
    margin-bottom: 20px

// title-middle
.title-middle
  font-size: 16px
  line-height: 1
  color: #fff
  +bold
  text-transform: uppercase

// title-small
.title-small
  +bold
  font-size: 14px

// accord
.accord
  border-bottom: 1px solid $gray

// footer
.footer
  padding: 30px 0 50px
  +r(920)
    padding: 0 0 50px
  &__in
    @extend %in
  &__right
    +r(620)
      margin-top: 15px
      width: 100%
      text-align: center
  &__col
    margin-left: 14px
  &__center
    margin-top: 24px
  .day
    line-height: 1
  strong
    +bold

// day
.day
  color: $yellow
  font-size: 40px
  +black

// partners
.partners
  margin-top: 50px

